.Menu {
  height: 2rem;
  width: 2rem;
  padding: 2px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  .Menu-bar {
    width: 100%;
    border-top: 3px solid lightgrey;
    border-bottom: 3px solid lightgrey;
    border-radius: 6px;
  }
}
